import { mapState, mapMutations, mapActions } from 'vuex'

export default {
  data () {
    return {
      thisCurrentTime: 0,
      thisTotalTime: 0,
      thisSliderValue: 0,
      isReplaceCurrentTime: true
    }
  },
  computed: {
    ...mapState([
      'currentTime',
      'totalTime',
      'isSpeedState'
    ])
  },
  methods: {
    ...mapMutations({
      setIsSpeedState: 'SET_IS_SPEED_STATE'
    }),
    ...mapActions([
      'setSliderChange',
      'myAudioPlay'
    ]),
    // 拖动过程中触发
    sliderChangingFn (e) {
      const num = e.mp.detail.value
      const data = {
        status: false,
        num
      }
      this.setSliderChange(data)
      this.setIsSpeedState(true)
      this.thisSliderValue = num
      this.isReplaceCurrentTime = false
    },
    // 完成一次拖动后触发
    sliderChangeFn () {
      this.setIsSpeedState(false)
      setTimeout(() => {
        this.myAudioPlay()
        this.isReplaceCurrentTime = true
      }, 500)
    },
    timeToMinute (times) {
      let t = ''
      if (times > -1) {
        const min = Math.floor(times / 60) % 60
        const sec = times % 60
        if (min < 10) {
          t = '0'
        }
        t += min + ':'
        if (sec < 10) {
          t += '0'
        }
        t += sec.toFixed(2)
      }
      t = t.substring(0, t.length - 3)
      return t
    }
  },
  watch: {
    currentTime (num) {
      this.thisCurrentTime = this.timeToMinute(num)
    },
    totalTime (num) {
      this.thisTotalTime = this.timeToMinute(num)
    }
  }
}
